body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main{

  height: 100vh;
  background-image: linear-gradient(antiquewhite, rgb(166, 164, 160));
  display: flex;
  align-items: center;
  justify-content: center;
}


.playerInfoGameBoard{
  max-width: 40%;
  min-width: 500px;
  max-height: 80%;
  height: 530px;
  border: 5px outset rgb(90, 87, 87);
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: rgb(52, 50, 47);
  padding: 20px;
  border-radius: 20px;
  box-shadow: 5px 2px 15px 5px;
  }
  #players{
    
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 80px;
    font-size: 1.2rem;
  }

  #playerInfoContainer{
    min-height: 60px;
    min-width: 200px;
  }
  .higlightPayer li.active{
    background-color: rgba(42, 165, 118, 0.433);
    padding-left: 3px;
    padding-bottom: 3px;
    border-radius: 0.8rem;
    box-shadow: 2px 2px 15px black;

  }


  .gameOver {
    max-width: 40%;
    min-width: 550px;
    height: 580px;
    background-color: rgba(174, 144, 205, 0.956);
    border: 10px outset rgb(61, 48, 61);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 3rem;

  }
.gameOver p{
  font-weight: bold;
}
.gameOver button{
  font-size: 1.5rem;
  background-color: blueviolet;
  padding: 10px;
  border-radius: 0.5rem;
  box-shadow: 1px 2px 10px 2px rgb(76, 53, 76);
  cursor: pointer;
}
#log{
  display: none;
}
h1{
  margin:1rem;
}